import React from "react";

const outputPage = (props) => {

    let temp;

    if (props.name) {

        if (props.temp > 0) {
            temp = "+" + props.temp
        } else {
            temp = props.temp
        }

        return (
            <>
                <div className="city"> Погода в городе : {props.name} </div>
                <div className="temp">
                    <h1>{temp}° </h1>  {/* ℃ */}
                </div>
                
                <div className="other-weather">
                    <div className="pressure"> 
                        <i className="fas fa-tachometer-alt"></i>
                        {props.pressure} мм. рт. ст.
                    </div>
                    <div className="wind"> 
                        <i className="fas fa-wind"></i>
                        {props.wind} м/с
                    </div>


                    <div 
                    className="humidity" >
                    <i className="fas fa-tint"></i>
                    {props.humidity}%
                </div>
                </div>

                
            </>
        )

    } 
    if (props.error) {
        if (props.failed) {
            return (
            <div> такого города нет :( </div>
            ) 
        }   
        return (
            <div> Введите город, в котором хотите узнать погоду</div>
        )
    } 
    if (props.failed) {
            console.log(2)
            return (
            <div> такого города нет :( </div>
            ) 
        }   
};

export default outputPage;